.circle {
  background-color: #fff;
  border-radius: 50%;
  border: 15px solid;
  padding: 0;
  transition: all 0.3s ease-in-out;

  &:hover {
    box-shadow: inset 0 0 10px #b3afb3;
  }

  &.scissors {
    border-color: #f1c94e;
    img {
      padding: 12.5px 17px;
    }
  }

  &.rock {
    border-color: #d44c4c;
    img {
      padding: 17.5px 18.5px;
    }
  }

  &.paper {
    border-color: #4eadf1;
    img {
      padding: 12px 18px;
    }
  }

  &.empty {
    width: 85px;
    height: 85px;
    border: none;
    background-color: #020024;
  }

  &.winner::after,  
  &.winner::before {
    content: "";
    position: absolute;
    width: 200px;
    height: 200px;
    background-color: #fff;
    border-radius: 50%;
    opacity: 0;
    animation: growAndFade 3s infinite;
    z-index: -1;
  }

  &.winner::after {
    animation-delay: 0s;    
  }
  
  &.winner::before {
    animation-delay: 1.5s; 
  }
}

@keyframes growAndFade {
  0% {
    opacity: .3;
    transform: scale(0.5);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}